.form {
  margin: 0 auto;
  width: 100%;
  max-width: 584px;
  margin-top: 26px;

  &--small {
    margin: 0;

    .input {
      min-height: 30px;
    }
  }
}

.input {
  margin-top: 6px;
  border: 1px solid $color-secondary;
  display: flex;
  position: relative;
  min-height: 44px;
  box-shadow: none;
  border-radius: 24px;
  margin: 0 auto;
  width: auto;
  max-width: 584px;
  align-items: center;

  @include respond(tab-land) {
    min-height: 40px;
  }
  @include respond(tab-port) {
    min-height: 34px;
  }
  @include respond(phone) {
    min-height: 30px;
  }
}

.input--icon {
  width: 20px;
  height: 20px;
  fill: $color-primary;
  margin: 0 14px;

  &:not(:first-child) {
    cursor: pointer;
  }
}

.input--box {
  border: none;
  outline: none;
  font-size: 1rem;
  flex-grow: 1;
  min-width: 50px;
}

.button {
  display: block;
  margin: 0 auto;
  margin-top: 29px;
  border: none;
  border-radius: 4px;
  font-size: 14px;
  padding: 0 16px;
  line-height: 27px;
  height: 36px;
  min-width: 54px;
  text-align: center;
  cursor: pointer;
  user-select: none;
}
