.navbar {
  display: flex;
  justify-content: space-between;
  align-items: end;
  width: 100%;
  height: 100%;
  padding: 10px 20px;
  gap: 10px;

  &--left {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    gap: 8px;
    height: 32px;
    min-width: 200px;
    flex: 1;
  }

  &--right {
    display: flex;
    align-items: center;
  }
}
